html,
body {
  margin: 0;
  padding: 0;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
}

.Main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  background: url(/veber/static/media/bg-min.c331c969.jpg) no-repeat
    center center fixed;
  background-size: cover;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: auto;
  background: #181818;
  color: white;
}

.ButtonBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
}

.Button {
  width: 180px;
  height: 30px;
  background-color: #e0e0e0;
  border-radius: 16px;
  text-decoration: none;
}

.Column {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 20px;
  
}

.Veber-logo {
  height: 10vmin;
  pointer-events: none;
  top: 10px;
  left: 0px;
  position: absolute;
}

break {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  width: 0px; 
  height: 0px; 
  overflow: hidden;
}
